import React, {Component} from 'react'
import {connect} from 'dva'
import {Button, Select, Radio} from 'antd'
import UtilGlobal from '../../utils/global'
import {showTime, parseTime} from '@/utils/utils'

import BaseForm from '@/components/BaseForm'
import BaseTable from '@/components/BaseTable'
import BaseContent from '@/components/BaseContent'

import styles from './style.module.less'
const Option = Select.Option;
const TABLE_KEY = 'exam_exam_id';
const {basePath} = UtilGlobal;

let mapState = store=>{
  return {...store.examList, ...store.main}
}
let mapDispatch = dispatch => {
  return {
    initData(payload){
      dispatch({type:'examList/requestData', ...payload});
    },
    getExamList (payload) {
      dispatch({type:'examList/examList', ...payload});
    }
  }
}

@connect(mapState,mapDispatch)
class ExamList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      subject_id: '',
      questions_type_id: '',
      exam_id: ''
    }
  }

  componentDidMount () {
    this.props.initData()
    this.props.getExamList()
  }

  get renderForm () {
    const {subject, examType} = this.props
    return (
      <BaseForm
        items={[
          {
            label: '',
            field: 'exam_id',
            content: (
              <Select>
                {examType.map((item, index) => <Option value={item.exam_id} key={index}>{item.exam_name}</Option>)}
              </Select>
            ),
          },
          {
            label: '',
            field: 'subject',
            content: (
              <Select>
                {subject.map((item, index) => <Option value={item.subject_id} key={index}>{item.subject_text}</Option>)}
              </Select>
            ),
          },
          {
            isFormItem: false,
            content: (
                <Button
                    type="primary"
                    icon="search"
                    htmlType="submit"
                    style={{
                        margin: '3px 0 0 50px',
                    }}
                >
                </Button>
            ),
          }
        ]}
        onSubmit={this.handleFormSubmit}
      />
    )
  }

  handlerClick = (id) => {
    this.props.history.push({
      pathname: basePath + '/main/exam/detail',
      search: `?id=${id}`,
      state:{
        parent:'',
        child:'',
        id:'main-examDetail'
      }
    })
  }
  
  get columns () {
    const columns = [{
      title: '',
      key: 'questions_type_id',
      render: (item) => {
        return (
          <React.Fragment>
            <div>
              <h5>{item.title}</h5>
              <p style={{fontSize: 12}}>
                <span style={{marginRight: 10}}>{showTime(item.end_time - item.start_time)}</span>
                <span>{item.number}</span>
                <span>0</span>
              </p>
            </div>
          </React.Fragment>
        );
      }
    }, {
      title: '',
      key: 'questions_type_text',
      render: (item) => {
        return (
          <React.Fragment>
            <div>
              <h5></h5>
              <p style={{fontSize: 12}}>{item.grade_name.join('  ')}</p>
            </div>
          </React.Fragment>
        );
      }
    }, {
      title: '',
      dataIndex: 'user_name',
      key: 'user_name',
    }, {
      title: '',
      key: 'start_time',
      render: (item) => {
        return (
          <p style={{fontSize: 12}}>{parseTime(item.start_time)}</p>
        )
      }
    }, {
      title: '',
      key: 'end_time',
      render: (item) => {
        return (
          <p style={{fontSize: 12}}>{parseTime(item.end_time)}</p>
        )
      }
    }, {
      title: '',
      key: 'action',
      render: (item) => {
        return (
          <React.Fragment>
            <div>
              <a href="javascript:;" onClick={() => this.handlerClick(item.exam_exam_id)}></a>
            </div>
          </React.Fragment>
        );
      }
    }];
    return columns
  }

  get renderTable () {
    const {examList} = this.props
    return (
      <BaseTable
        columns={this.columns}
        dataSource={examList}
        rowKey={TABLE_KEY}
        wrapStyle={{
            padding: 0,
        }}
        buttons={
          [
            <div className={styles.tool}>
              <h4 style={{margin: 0}}></h4>
              <Radio.Group defaultValue="a">
                <Radio.Button value="a"></Radio.Button>
                <Radio.Button value="b"></Radio.Button>
                <Radio.Button value="c"></Radio.Button>
              </Radio.Group>
            </div>
          ]
        }
      />
    )
  }

  
  render() {
    return (
      <React.Fragment>
        <BaseContent>
          {this.renderForm}
        </BaseContent>
        <BaseContent>
          {this.renderTable}
        </BaseContent>
      </React.Fragment>
    )
  }
  
}
export default ExamList
